import { Icon } from '@iconify/react';

const Notice = () => {
  return (
    <div className="notice">
      <div className="title-wrap">
        <h1 className="notice-title"></h1>
        <button className="more-button">
          <Icon icon="ic:round-plus" width="1.6rem" />
        </button>
      </div>
      {/*   */}
      <div className="writing-wrap">
        <Icon className="writing-icon" icon="material-symbols:circle" />
        <div className="writing">
          {`[`}{`]`} 2024 1  PPT   
        </div>
      </div>
      <div className="writing-wrap">
        <Icon className="writing-icon" icon="material-symbols:circle" />
        <div className="writing">
          ' e-Class'   (01/24 () 16:30 ~ 17:30)
        </div>
      </div>
      <div className="writing-wrap">
        <Icon className="writing-icon" icon="material-symbols:circle" />
        <div className="writing">' e-Class'</div>
      </div>
    </div>
  );
};

export default Notice;
