import { useState } from 'react';
import axios from 'axios';
const LoginForm = () => {
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');

  const changeId = (e) => {
    setId(e.target.value);
    console.log(id);
  };

  const changePW = (e) => {
    setPassword(e.target.value);
  };

  const submit = (e) => {
    e.preventDefault();
    // changeId();
    // changePW();
    if (!id || !password) {
      alert(' ');
    }
  };
  axios
    .post('/api/login', {
      id: id,
      password: password,
    })
    .then((response) => {
      console.log(response.data.id);
      console.log(response.data.password);
      if (response.data.email === undefined) {
        // id    userId = undefined, msg = ' id   .'
        console.log('======================', response.data.msg);
        alert(' id   .');
      } else if (response.data.email === null) {
        // id , pw    userId = null , msg = undefined
        console.log(
          '======================',
          '    .',
        );
        alert('    .');
      } else if (response.data.email === id) {
        // id, pw   userId = userId1, msg = undefined
        console.log('======================', ' ');
        sessionStorage.setItem('userId', id); // sessionStorage id user_id key  
        sessionStorage.setItem('userPassword', password); // sessionStorage id user_id key  
      }
      //     ()
      document.location.href = '/';
    })
    .catch((err) => {
      console.log(err);
    });
  return (
    <div className="login-left">
      <form onSubmit={submit}>
        <div className="login-form">
          <div className="login">
            <input
              className="id"
              placeholder="  "
              onChange={changeId}
            />
            <input
              className="password"
              placeholder=" "
              type="password"
              onChange={changePW}
            />
          </div>
          <div className="button-div">
            <button className="login-button"></button>
          </div>
        </div>
      </form>

      {/*   */}
      <div className="saveId">
        <input className="check-box" type="checkbox" />
        <p> </p>
      </div>

      {/*   */}
      <div className="find-id">
        <p>/ </p>
      </div>
    </div>
  );
};

export default LoginForm;
